import React, {Component} from 'react';
import PropTypes from 'prop-types'
import axios from 'axios';
import {connect} from 'react-redux';
import Actions from '../../actions/Creators'

class SaveFrom extends Component {
    constructor(props) {
        super(props)
        this.state = {
            url: ''
        }
        this.doPost = this.doPost.bind(this)
    }

    doPost() {
        this.props.dispatch(Actions.postUrl(this.state))
    }

    componentWillUpdate(newProps, newState) {
        if (this.state.url !== newState.url) {
            this.props.dispatch(Actions.postUrl(newState))
        }
    }

    render() {
        const {isPosted, livestreams, isPosting} = this.props
        return (
            <div className="container" style={{marginTop: '80px'}}>
                {
                    isPosting && <div className="loading">Loading&#8230;</div>
                }
                <div className="row justify-content-center">
                    <div className="col-md-8 col-12">
                        <div className="input-group">
                            <input type="text" className="form-control" placeholder="Insert A Link..."
                                   value={this.state.url}
                                   onChange={(e) => this.setState({url: e.target.value})}
                            />
                            <span className="input-group-btn">
                            <button className="btn btn-secondary" type="button"
                                    onClick={this.doPost}
                            >Go!</button>
                          </span>
                        </div>
                    </div>
                </div>
                {
                    isPosted &&
                    <div className="row justify-content-center" style={{marginTop: '15px'}}>
                        <div className="col-md-4 col-12">
                            <div className="thum-box">
                                <img src={livestreams.thumbnail} alt=""/>
                            </div>
                        </div>
                        <div className="col-md-4 col-12">
                            <p>{livestreams.fulltitle}</p>
                            <div className="btn-group">
                                <button type="button" className="btn btn-success">Download</button>
                                <button type="button" className="btn btn-success dropdown-toggle dropdown-toggle-split"
                                        data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                                    <span className="sr-only">Toggle Dropdown</span>
                                </button>
                                <div className="dropdown-menu">
                                    {
                                        livestreams.formats.map((item, key) => (
                                            <a className="dropdown-item" href={item.url} download={true}
                                               key={key}>{item.format}</a>
                                        ))
                                    }
                                </div>
                            </div>
                        </div>
                    </div>
                }
            </div>
        )
    }
}

const mapStateToProps = (state) => {
    return {
        livestreams: state.livestreams.data,
        isPosted: state.livestreams.urlPosted,
        isPosting: state.livestreams.urlPosting
    }
};

export default connect(mapStateToProps)(SaveFrom)


